.main-footer {
  height: 243px;
  background-color: $white;
  position: relative;
  z-index: 2;
  border-radius: 30px 30px 0 0;
}

.main-footer__logo-image {
  padding-top: 38px;
  width: 100px;
  display: block;
  margin: 0 auto;
  margin-bottom: 41px;
}

.main-footer__social-list {
  display: flex;
  width: 135px;
  margin: 0 auto;
  justify-content: space-between;
  margin-bottom: 52px;
}


.main-footer__social-list .social-item {
  margin: 0;
  padding: 0;
 z-index: 2;
  position: relative;
  fill: $navy-blue;
  &::after {
    position: absolute;
    content: "";
    background-color: #ffd74b;
    width: 45px;
    height: 45px;
    border-radius: 50%;
    top: -10px;
    left: -16px;
    z-index: -1;
  }
}

.main-footer__social-list .social-item:nth-child(2)::after {
  left: -10px;
}
